import React from "react";
import styled from "styled-components";
import Input from "../../components/Input";
import { Link } from "react-router-dom";
import { LabelMobile } from "../../components/Label";
import MobileViewTemplate, {
  MobileFormWrapper,
} from "../../components/templates/MobileViewTemplate";
import SmallButton from "../../components/buttons/SmallButton";
import {
  Title,
  Description,
  StyledAnchor,
  ShiftedRowButtonContainer,
  SingUpInfoWrapper,
  SingUpInfoMark,
  SingUpInfoParagraph,
  MobileLinkButton,
} from "../../components/SignInUpElements";

const SignUpMobile = () => {
  return (
    <MobileViewTemplate>
      <MobileFormWrapper>
        <Title>Za konto</Title>
        <LabelMobile htmlFor="firstName">{"Imi"}</LabelMobile>
        <Input id="firstName" name="firstName" type="text" />
        <LabelMobile htmlFor="secondName">{"Nazwisko"}</LabelMobile>
        <Input id="secondName" name="secondName" type="text" />
        <LabelMobile htmlFor="email">{"Email"}</LabelMobile>
        <Input id="email" name="email" type="email" />
        <LabelMobile htmlFor="password">{"Haso"}</LabelMobile>
        <Input id="password" name="password" type="password" />
        <Description>
          Masz ju konto? Zaloguj si{" "}
          <Link to="../pages/SignInMobile">
            <StyledAnchor>tutaj</StyledAnchor>
          </Link>
        </Description>
        <SingUpInfoWrapper>
          <SingUpInfoMark>!</SingUpInfoMark>
          <SingUpInfoParagraph>
            Wypenij nasz szczegowy formularz, eby korzysta ze wszystkich
            moliwoci, zrb to teraz i miej to z gowy!
          </SingUpInfoParagraph>
          <SingUpInfoParagraph>
            Moesz te pomin ten krok i przyglda si naszemu portalowi z
            nieco dalszej odlegoci!
          </SingUpInfoParagraph>
        </SingUpInfoWrapper>
        <ShiftedRowButtonContainer>
          <SmallButton type="submit" label="Stwrz konto" />
          <MobileLinkButton to="/pages/SignUpFull1Mobile">
            Wepnij formularz
          </MobileLinkButton>
        </ShiftedRowButtonContainer>
      </MobileFormWrapper>
    </MobileViewTemplate>
  );
};

export default SignUpMobile;
